"use client";

import { useState, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";

interface Props {
  onUpload: (projectData: {
    projectId: string;
    originalUrl: string;
    modifiedUrl: string | null;
  }) => void;
}

export default function AudioUploader({ onUpload }: Props) {
  const [isDragOver, setIsDragOver] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);

    const files = Array.from(e.dataTransfer.files);
    if (files.length > 0) {
      await uploadFile(files[0]);
    }
  };

  const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      await uploadFile(files[0]);
    }
  };

  const uploadFile = async (file: File) => {
    if (!file.type.includes('audio/')) {
      alert('Please upload an audio file (MP3, WAV, etc.)');
      return;
    }

    setUploading(true);
    setUploadProgress(0);

    // Simulate upload progress
    const progressInterval = setInterval(() => {
      setUploadProgress(prev => {
        if (prev >= 90) {
          clearInterval(progressInterval);
          return 90;
        }
        return prev + 10;
      });
    }, 200);

    try {
      // Create a temporary project ID for demo
      const projectId = `project-${Date.now()}`;
      const originalUrl = `${window.location.origin}/api/files/${projectId}/original.wav`;

      // Simulate backend upload delay
      setTimeout(() => {
        clearInterval(progressInterval);
        setUploadProgress(100);

        setTimeout(() => {
          setUploading(false);
          onUpload({
            projectId,
            originalUrl,
            modifiedUrl: null
          });
        }, 500);
      }, 2000);
    } catch (error) {
      console.error('Upload failed:', error);
      setUploading(false);
      clearInterval(progressInterval);
    }
  };

  return (
    <div className="w-full max-w-2xl mx-auto">
      <motion.div
        whileHover={{ scale: 1.02 }}
        className="relative"
      >
        <motion.div
          className={`w-full h-80 border-2 border-dashed rounded-2xl transition-all duration-300 cursor-pointer ${
            isDragOver
              ? 'border-purple-400 bg-purple-400/10 scale-105'
              : 'border-white/20 bg-white/5 hover:border-white/30 hover:bg-white/10'
          } ${uploading ? 'pointer-events-none' : ''}`}
          onClick={() => !uploading && fileInputRef.current?.click()}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
          animate={isDragOver ? { scale: 1.05, borderColor: '#a855f7' } : { scale: 1, borderColor: 'rgba(255, 255, 255, 0.2)' }}
        >
          <input
            ref={fileInputRef}
            type="file"
            accept="audio/*"
            onChange={handleFileSelect}
            className="hidden"
          />

          <div className="flex flex-col items-center justify-center h-full p-8 text-center">
            <AnimatePresence mode="wait">
              {uploading ? (
                <motion.div
                  key="uploading"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  className="flex flex-col items-center space-y-4"
                >
                  <div className="relative">
                    <svg className="w-16 h-16 text-purple-400 animate-spin" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                  </div>
                  <div className="w-full max-w-xs bg-white/10 rounded-full h-2">
                    <motion.div
                      className="bg-gradient-to-r from-purple-400 to-pink-400 h-2 rounded-full"
                      style={{ width: `${uploadProgress}%` }}
                      initial={{ width: 0 }}
                      animate={{ width: `${uploadProgress}%` }}
                      transition={{ duration: 0.3 }}
                    />
                  </div>
                  <p className="text-purple-200 font-medium">Analyzing your audio...</p>
                </motion.div>
              ) : (
                <motion.div
                  key="idle"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  className="flex flex-col items-center space-y-6"
                >
                  <motion.div
                    animate={{
                      scale: isDragOver ? 1.2 : 1,
                      rotate: isDragOver ? 5 : 0
                    }}
                    transition={{ duration: 0.2 }}
                  >
                    <svg className="w-20 h-20 text-white/60" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                    </svg>
                  </motion.div>

                  <div className="space-y-2">
                    <h3 className="text-2xl font-bold text-white">
                      {isDragOver ? 'Drop your audio file here' : 'Upload your audio track'}
                    </h3>
                    <p className="text-white/70 max-w-md">
                      Drag and drop or click to upload MP3, WAV, or other audio files for processing with NOEM
                    </p>
                  </div>

                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="px-8 py-3 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full font-medium text-white hover:from-purple-600 hover:to-pink-600 transition-all duration-200"
                    onClick={(e) => {
                      e.stopPropagation();
                      fileInputRef.current?.click();
                    }}
                  >
                    Choose File
                  </motion.button>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        </motion.div>
      </motion.div>
    </div>
  );
}
